  top: 10px;
  font-size: 25px;
  right: 20px; }

.react-tabs__tab-list {
  border-bottom: 1px solid #aaa;
  margin: 0 0 10px;
  padding: 0; }

.react-tabs__tab {
  display: inline-block;
