.NextAndPrevious .Button {
  outline: 0;
  border: 1px solid #d9d9d9;
  background-color: #000;
  border-radius: 4px;
  font-family: Arial;
  padding: 10px;
  color: white;
  transition: color .3s ease;
  margin-right: 15px; }
  .NextAndPrevious .Button:hover {
    border-color: red;
    color: red; }
  .NextAndPrevious .Button.disable {
    border-color: #d9d9d9;
    color: rgba(255, 255, 255, 0.25);
    cursor: not-allowed; }



